import request from '../utils/request';

const add = data =>
  request({
    url: '/app/moments/add',
    method: 'post',
    data,
  });

const list = page =>
  request({
    url: '/app/moments/list',
    method: 'get',
    params: {
      page,
    },
  });
const get = id =>
  request({
    url: '/app/moments/detail',
    method: 'get',
    params: {
      id,
    },
  });
const upvote = id =>
  request({
    url: '/app/moments/upvote',
    method: 'get',
    params: {
      id,
    },
  });
export default {
  add,
  list,
  get,
  upvote,
};



