<template>
  <div class="hot-container">
    <div class="media">
      <mt-swipe :auto="0">
        <mt-swipe-item v-for="(image, i) in item.images" :key="i"> 
          <img class="swipe-image" :src="image" alt="">
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <div class="content">
      <div class="back" @click="handleBack">
        <img src="../assets/back.svg" alt="">
      </div>
      <div class="buttons">
        <div class="head" @click="handleFollow(item.user_id, 'user', item.isFollow)">
          <img class="headImg" :src="item.headimgurl" alt="">
          <img v-show="item.isFollow" class="add" src="../assets/add.svg" alt="">
        </div>
        <div class="like" @click="handleUpvote">
          <img src="../assets/goal-like.svg" alt="">
          <span>{{item.upvote_number}}</span>
        </div>
        <div class="comment" @click="openLayer(1)">
          <img src="../assets/hot-comment.svg" alt="">
          <span>{{item.comment_number}}</span>
        </div>
        <div class="recommend" @click="openLayer(2)">
          <span>荐</span>
        </div>
      </div>
      <div class="bottom">
        <p>
          {{item.description}}
        </p>
      </div>
    </div>
    <div class="top-layer" v-if="layer === 2">
      <div class="close" @click="handleClose">
        <img src="../assets/close.svg" alt="">
      </div>
      <h2>推荐活动</h2>
      <div class="box">
        <div class="activity-wrap">
          <div class="activity" v-for="(ii, i) in [1,2,3,4]" :key="i" >
            <img :src="item.images[0]" alt="">
            <div class="text-wrap">
              <div class="activity-title">{{item.description}}</div>
              <div class="activity-time">2109.06.12</div>
              <div class="activity-price">{{ii}}元/位</div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="top-layer" v-if="layer === 1">
      <div class="close" @click="handleClose">
        <img src="../assets/close.svg" alt="">
      </div>
      <h2>全部评论</h2>
      <div class="box">
        <comment :item="item" :list="comments" :loadMore="loadMore" :reload="reload" :options="options"></comment>
      </div>
        <div class="comment-input">
          <input type="text" v-model="commentValue">
          <button @click="handleCommentSubmit" >发送</button>
        </div>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex';

import api from '../api/hot';
import CommentApi from '../api/comments';
import Comment from '../components/Comment';

export default {
  name: 'hot-detail',
  data() {
    return {
      options: {
        type: 'moment',
      },
      page: 1,
      item: {},
      layer: 0,
      commentValue: '',
    };
  },
  components: { Comment },

  created() {
    this.id = this.$route.params.id;
    this.type = 'moment';
  },
  mounted() {
    this.handleData();
  },
  computed: {
    ...mapState({
      comments: state => state.comments.list.moment,
    }),
  },
  methods: {
    async handleData() {
      const res = await api.get(this.id);
      console.log(res);
      const item = {
        ...res.data,
        images: res.data.images.map(v => 'http://ghw.work2pix.top' + v),
      };
      console.log(item);
      this.item = item;
    },
    handleBack() {
      this.$router.replace({
        name: 'Index',
      });
    },
    handleFollow(id, type, follow) {
      const cb = () => this.handleIn();
      if (follow) {
        this.$store.dispatch('unfollow', { id, type, cb });
      } else {
        this.$store.dispatch('follow', { id, type, cb });
      }
    },
    async handleUpvote() {
      const res = await api.upvote(this.id);
      if (res.code === 0) {
        this.item.upvote_number += 1;
      }
    },
    handleClose() {
      this.layer = 0;
    },
    openLayer(i) {
      this.layer = i;
    },
    loadMore() {
      const id = this.$route.params.id;

      this.$store.dispatch('getCommentsList', {
        type: this.type,
        id,
        page: this.page,
      });
      this.page += 1;
    },
    reload() {
      const id = this.$route.params.id;
      this.page = 1;

      this.$store.dispatch('regetCommentsList', {
        type: this.type,
        id,
        page: this.page,
      });
      this.page += 1;
    },
    async handleCommentSubmit() {
      const res = await CommentApi.comment(this.type)(this.id)(
        this.commentValue,
      );
      this.reload();
      if (res.code === 0) {
        this.commentValue = '';
        this.item.comment_number += 1;
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.hot-container {
  position: absolute;
  width: 100%;
  height: 100%;
  background: black;
}
.media {
  width: 100%;
  height: 100%;
  img {
    width: 100%;
  }
}
.back {
  position: absolute;
  top: 20px;
  left: 15px;
  img {
    width: 30px;
    height: 30px;
  }
}
.bottom {
  width: 100%;
  position: absolute;
  bottom: 0;
  padding: 17px 14px;
  color: white;
}
.buttons {
  position: absolute;
  top: 244px;
  right: 5px;
  width: 50px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  > div {
    margin-top: 15px;
  }
  .head {
    width: 100%;
    position: relative;

    .headImg {
      width: 100%;
      border-radius: 50%;
    }
    .add {
      position: absolute;
      bottom: -5px;
      width: 30%;
      left: 50%;
      transform: translate(-50%);
    }
  }

  .like,
  .comment,
  .recommend {
    width: 100%;
    img {
      position: relative;
      width: 80%;
      left: 10%;
    }
    span {
      display: block;
      color: white;
      text-align: center;
    }
  }
  .recommend {
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(255, 255, 255, 0.5);
    width: 40px;
    height: 40px;
    border-radius: 50%;
    span {
      color: black;
    }
  }
}

.top-layer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 70%;
  border-radius: 10px 10px 0 0;
  background: #f2f2f2;
  padding: 10px 15px;
  overflow: hidden;
  h2 {
    text-align: center;
    margin: 10px 0 15px;
  }
  .box {
    height: calc(100% - 54px);
    width: 100%;
    overflow: scroll;
  }
  .close {
    width: 20px;
    height: 20px;
    position: absolute;
    right: 15px;
    top: 15px;
    img {
      width: 100%;
    }
  }
}

.mint-swipe-items-wrap > .mint-swipe-item {
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
}
.activity-wrap {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.activity {
  width: 47%;
  flex-shrink: 0;
  background: white;
  margin: 10px 0;
  border-radius: 5px;
  overflow: hidden;
  img {
    width: 100%;
  }
  .text-wrap {
    padding: 5px 3px;
    .activity-title {
      font-size: 15px;
      color: #333;
      overflow: hidden;
      text-overflow: ellipsis;
      display: box;
      display: -webkit-box;
      line-clamp: 1;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
    }
    .activity-time {
      font-size: 13px;
      color: #666;
    }
    .activity-price {
      font-size: 20px;
      color: #d4504b;
      text-align: right;
    }
  }
}
</style>





