import api from '../../api/hot';

const hotModule = {
  state: {
    list: [],
    page: 0,
  },
  mutations: {
    setHotList(state, list) {
      state.list.push(...list);
    },
    hotPageInc(state) {
      state.page += 1;
    },
  },
  actions: {
    async getHotList({ commit, state }) {
      commit('hotPageInc');
      const res = await api.list(state.page);
      commit('setHotList', res.data);
    },
  },
};

export default hotModule;



