<template>
  <div class="new">
    <textarea
      class="textarea"
      auto-focus
      v-model="form.description"
      maxlength="-1"
      placeholder="这一刻的想法"
      placeholder-style="color: #999;">
    </textarea>
    <div class="image-list">
      <!-- preview the image which already be choosed -->
      <div
        class="choosed-image-list"
        v-for="(image, idx) in form.images"
        :key="idx">
        <img
          :src="image"
          mode="aspectFill"
          @click="previewImage(image,form.images)"
          class="choosed-image">
        <div
          class="choosed-image-delete-btn"
          @click="deleteImage(idx)">
          <img
            class="choosed-image-delete-btn-icon"
            src="../assets/close.svg">
        </div>
      </div>
      <!-- image choosing btn -->
      <div
        class="choose-image-btn"
        v-if="images.length < 9"
        @click="selectImages">
        <img
          class="choose-image-btn-icon"
          src="../assets/choose-image.svg">
      </div>
    </div>
    
    <div class="submit-btn">
      <button
        type="primary"
        size="default"
        @click="send"
        class="submit"
        :disabled="disabled">
        发送
      </button>
    </div>
  </div>
</template>
<script>
import wx from 'weixin-js-sdk';
import apiwx from '../api/wx';
import api from '../api/hot';

export default {
  name: 'moment',
  data() {
    return {
      form: {
        type: 1,
        description: '',
        images: [],
        tags: '',
        address: '',
      },
      type: 1,
      description: '',
      images: [],
      tags: '',
      address: '',
      location: '',
      latitude: '',
      longitude: '',
      disabled: false,
    };
  },
  methods: {
    uploadImage(func) {
      const setImage = func.bind(this);
      wx.chooseImage({
        count: 9,
        sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
        sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
        success: res => {
          const localIds = res.localIds; // 返回选定照片的本地ID列表，localId可以作为img标签的src属性显示图片
          this.images.push(...localIds);
          localIds.forEach(id => {
            wx.uploadImage({
              localId: id, // 需要上传的图片的本地ID，由chooseImage接口获得
              isShowProgressTips: 1, // 默认为1，显示进度提示
              success: async res1 => {
                const serverId = res1.serverId; // 返回图片的服务器端ID
                const response = await apiwx.uploadImage(serverId);
                setImage('http://ghw.work2pix.top' + response.data);
              },
            });
          });
        },
      });
    },
    deleteImage(idx) {
      this.form.images.splice(idx, 1);
    },
    selectImages() {
      this.uploadImage(image => {
        this.form.images.push(image);
      });
    },
    async send() {
      const res = await api.add(this.form);
    },
  },
};
</script>
<style lang="scss" scoped>
.new {
  width: 100%;

  .textarea {
    padding: 15px;
    width: 100%;
    color: #333;
    min-height: 120px;
    background: #fff;
    font-size: 16px;
    line-height: 36px;
    resize: none;
    outline: none;
    border: 0;
  }

  .image-list {
    padding: 15px;
    padding-top: 0;
    width: 100%;
    background: #fff;
    overflow: hidden;

    .choosed-image-list {
      position: relative;
      float: left;
      width: 70px;
      height: 70px;
      margin-right: 15px;
      margin-top: 15px;

      .choosed-image {
        width: 100%;
        height: 100%;
      }

      .choosed-image-delete-btn {
        position: absolute;
        right: -10px;
        top: -10px;
        width: 20px;
        height: 20px;
      }

      .choosed-image-delete-btn-icon {
        width: 20px;
        height: 20px;
      }
    }

    .choose-image-btn {
      float: left;
      width: 70px;
      height: 70px;
      margin-top: 15px;

      .choose-image-btn-icon {
        width: 70px;
        height: 70px;
      }
    }
  }

  .isanonymous {
    background: #fff;
    padding: 15px;
    padding-top: 0;
    overflow: hidden;

    .location {
      float: left;
      width: 250px;
      height: 32px;

      .location-icon {
        width: 32px;
        height: 32px;
        vertical-align: middle;
      }

      text {
        font-size: 14px;
        line-height: 32px;
        color: #666;
      }
    }
    .location-active {
      text {
        font-weight: bold;
        color: #fa6570;
      }
    }

    .isanonymous-btn {
      position: relative;
      float: right;
      width: 66px;
      height: 32px;
      padding: 0 6px;
      border-radius: 18px;
      transition: all 300ms ease;

      text {
        color: #fff;
        font-size: 12px;
        line-height: 32px;
      }
      .anonymous-text {
        float: left;
      }
      .named-text {
        float: right;
      }

      .isanonymous-btn-key {
        position: absolute;
        left: 1px;
        top: 1px;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        background: #fff;
        box-shadow: 0 2px 3px rgba(0, 0, 0, 0.4);
        transition: all 300ms ease;
      }
    }
    .anonymous {
      background: #fa6570;

      .isanonymous-btn-key {
        transform: translateX(34px);
      }
    }
    .named {
      background: #2ecc71;

      .isanonymous-btn-key {
        transform: translateX(0);
      }
    }
  }

  .submit-btn {
    margin: 0 auto;
    margin-top: 20px;
    width: 90%;

    .submit {
      width: 80%;
      margin: 50px 10%;
      background: #fdda06;
      padding: 15px;
      border: 0;
      border-radius: 5px;
      outline: none;
    }

    button[disabled][type='primary'] {
      opacity: 0.7;
    }
  }
}
</style>



